@import url(//fonts.googleapis.com/css2?family=Mr+Bedfort&display=swap);
@import url(//fonts.googleapis.com/css2?family=Libre+Franklin:wght@300&display=swap);
.nav-item {
    border: 1px solid black;
    padding: 0% 1%;
    margin: 5% 0% 0% 5%;
    box-shadow: 5px 5px grey;
}

#About {
padding-bottom: 0;
}

.about-container {
    max-width: 50%;
    margin-bottom:10%;
}

.about-title {
font-family: 'Mr Bedfort', cursive;
font-size: 3rem;
}

.about-text {
line-height: 1.75rem;
}

.about-signature {
float:right;
}

about-image {
width: 10px;
}

.about-sunflower {
    position:relative;
    bottom: 50px;
    margin-left: -15%;
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}

.work-title {
    font-family: 'Libre Franklin', sans-serif;
    text-align: center;
    padding-top: 5%;
}

.resume-container {
padding: 0% 12%;
}

.text-dark {
    text-align: left;
}

hr {
    margin: 3% auto;
    border: 8px dotted #eaf6f6;
    border-bottom: none;
    width: 3%;
}
.skills-title {
    font-family: 'Libre Franklin', sans-serif;
    text-align: center;
    padding-top: 5%;
}

.progress-bar {
    background-color: pink;
}

.progress-container {
padding: 0% 15% 5%;
}

.pic-container {
padding: 2% 0%;
text-align:center;
}
.pic-seperator {
width: 30%;
}

.contact-container {
margin-top:5%;
text-align:center;
padding: 6% 10% 0%;
}

.feature-title {
    margin-top: 5%;
    font-family: 'Libre Franklin', sans-serif;
}

.feature-text {
line-height:1,5rem;
}

.row-contact {
margin: 0;
}

.repos {
text-align:center;
}
